  object-fit: contain;
}

.nav-link {
  font-size: 1.5em !important;
  padding: 1vw!important;
  color: white!important;
}

.nav-link:hover {
  color: rgb(0, 0, 0)!important;
}

.navbar-brand {
  font-size: 1.5em !important;
  color: white!important;
}

.my-4 {
  margin-top: 1.5rem !important;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 1;
  transition: .5s ease;
  background-color: #008bba7e;
}

.hovercontainer:hover .overlay {
  opacity: 1;
}

